<template>
  <div class="upload-file-container">
    <!--  -->
    <div class="upload-wrap" @click="onUploadWrap">
      <img
        :src="value ? value : publishImage"
        alt=""
        class="upload-img"
        ref="publish-cover"
      />
      <!--  -->
      <img src="./add.png" alt="" class="add-image" ref="addImage" />
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="uploadDialogVisible"
      append-to-body
      @opened="onOpen"
    >
      <el-tabs type="border-card" v-model="activeName">
        <el-tab-pane label="" name="image">
          <!--  -->
          <image-list
            :isShowAction="false"
            :isShowAdd="false"
            :isShowRow="6"
            :isShowPageSize="8"
            :isShowPreview="true"
            :isShowSelected="true"
            :isShowAlert="true"
            ref="imageList"
          ></image-list>
        </el-tab-pane>
        <el-tab-pane label="" class="cover-container" name="upload">
          <div class="cover-wrap" @click="$refs.uploadCover.click()">
            <div class="cover-wrap-border">
              <img ref="cover" alt="" class="cover-wrap-image" />
            </div>
            <el-tag class="cover-title"></el-tag>
          </div>
          <input type="file" hidden ref="uploadCover" @change="coverChange" />
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="uploadDialogVisible = false"> </el-button>
        <el-button type="primary" @click="onUploadCover"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getImgUrl } from "@/api/image.js";
import imageList from "@/views/Image/components/image-list.vue";
export default {
  name: "uploadFileIndex",
  components: { imageList },
  // props: ["coverImage"],
  props: ["value"],
  data() {
    return {
      uploadDialogVisible: false, //
      previewImage: "", //
      activeName: "image", //
      publishImage: "", //
    };
  },
  watch: {},
  computed: {},
  methods: {
    //
    onUploadWrap() {
      this.uploadDialogVisible = true;
    },
    //input
    coverChange() {
      const file = this.$refs["uploadCover"].files[0];
      const blob = window.URL.createObjectURL(file);
      //
      this.$refs["cover"].src = blob;
    },
    //
    async onUploadCover() {
      if (this.activeName === "upload") {
        const file = this.$refs.uploadCover.files[0];
        //
        if (!file) {
          this.$message("");
          return;
        }
        const fd = new FormData();
        fd.append("image", file);
        //url
        const { data: res } = await getImgUrl(fd);
        this.publishImage = res.data.url;
        //
        this.uploadDialogVisible = false;
        this.$message({
          message: "",
          type: "success",
        });
        //url
        // this.$emit("update-url", this.publishImage);
        this.$emit("input", this.publishImage);
        //
        this.$refs["addImage"].style = "display:none";
      } else if (this.activeName === "image") {
        //refs
        const imageList = this.$refs["imageList"];
        const selected = imageList.selected;
        if (selected === null) {
          this.$message("");
          return;
        }

        //
        this.uploadDialogVisible = false;
        //
        this.$emit("input", imageList.imageList[selected].url);
        //
        this.$refs["addImage"].style = "display:none";
        //
        this.$message({
          message: "",
          type: "success",
        });
      }
    },
    //
    onOpen() {
      //src
      this.$refs["cover"].src =
        "data:image/png;base64,";
    },
  },
  created() {},
  mounted() {
    //id
    if (this.$route.query.id) {
      //
      this.$refs["addImage"].style = "display:none";
    }
  },
};
</script>
