<template>
  <div class="image-list-container">
    <!--  -->
    <div class="nav-bar-btn">
      <el-radio-group v-model="collect" @change="onCurrentChange">
        <el-radio-button :label="false"></el-radio-button>
        <el-radio-button :label="true"></el-radio-button>
      </el-radio-group>
      <el-button
        type="success"
        size="mini"
        @click="uploadDialogVisible = true"
        v-if="isShowAdd"
        ></el-button
      >
    </div>
    <!--  -->
    <div class="img-list">
      <el-row :gutter="10" v-loading="loading">
        <el-col
          :xs="12"
          :sm="6"
          :md="6"
          :lg="isShowRow"
          v-for="(image, index) in imageList"
          :key="index"
          class="img-col"
        >
          <el-image
            class="img-item"
            :src="image.url"
            fit="cover"
            style="width: 250px;height:130px"
            :previewSrcList="isShowPreview ? [] : [image.url]"
            @click.native="selected = index"
          >
          </el-image>
          <div
            class="selected"
            v-if="isShowSelected && selected === index"
          ></div>
          <!--  -->
          <div class="icon-btn" v-if="isShowAction">
            <!--  -->
            <el-button
              :icon="
                image.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'
              "
              @click="onCollect(image)"
              size="small"
              circle
              :loading="image.loading"
              type="warning"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              circle
              size="small"
              @click="onDelete(image)"
            ></el-button>
          </div>
        </el-col>
      </el-row>
    </div>

    <!--  -->

    <el-dialog
      title=""
      :visible.sync="uploadDialogVisible"
      width="21%"
      append-to-body
    >
      <!--  -->
      <!-- actionname -->
      <el-upload
        class="upload-demo"
        action="http://api-toutiao-web.itheima.net/mp/v1_0/user/images"
        :headers="upLoadHeader"
        name="image"
        drag
        :on-success="onUploadSuccess"
        multiple
        :show-file-list="false"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">
          jpg/png500kb
        </div>
      </el-upload>
    </el-dialog>

    <!--  -->
    <el-pagination
      layout="prev, pager, next"
      :total="totalCount"
      :page-size="isShowPageSize"
      :current-page.sync="page"
      @current-change="onPageChange"
      :disabled="loading"
      background
      style="margin-bottom:20px"
    >
    </el-pagination>
    <el-alert title="" type="warning" center v-if="isShowAlert">
    </el-alert>
  </div>
</template>

<script>
import { getImgInfo, collectImg, deleteImg } from "@/api/image.js";
import { getItem } from "@/utils/storage.js";

export default {
  name: "imageListIndex",
  components: {},
  props: {
    //
    isShowAction: {
      type: Boolean,
      default: true,
    },
    //
    isShowRow: {
      type: Number,
      default: 4,
    },
    //
    isShowAdd: {
      type: Boolean,
      default: true,
    },
    //
    isShowPageSize: {
      type: Number,
      default: 18,
    },
    //
    isShowPreview: {
      type: [Array, Boolean],
      default: false,
    },
    //
    isShowSelected: {
      type: Boolean,
      default: false,
    },
    //
    isShowAlert: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    const user = getItem("user");
    return {
      collect: false,
      imageList: [],
      uploadDialogVisible: false, //
      //
      upLoadHeader: {
        Authorization: `Bearer ${user.token}`,
      },
      pageSize: 18, //
      totalCount: 0, //
      page: 1, //
      loading: false, //
      selected: null, //
    };
  },
  watch: {},
  computed: {},
  methods: {
    async loadImg(page = 1) {
      //
      this.loading = true;
      //
      this.page = page;
      const { data: res } = await getImgInfo({
        collect: this.collect,
        page,
        per_page: this.isShowPageSize,
      });
      const results = res.data.results;
      results.forEach((img) => {
        img.loading = false;
        img.deleteLoading = false;
      });
      this.totalCount = res.data.total_count;
      this.imageList = results;
      this.loading = false;
    },
    //
    onCurrentChange() {
      this.loadImg();
    },
    //
    onUploadSuccess() {
      this.uploadDialogVisible = false;
      this.$message({
        message: "",
        type: "success",
      });
      this.loadImg();
    },
    //
    onPageChange(page) {
      this.loadImg(page);
      //
      this.selected = null;
    },
    //
    async onCollect(image) {
      //
      image.loading = true;
      //
      await collectImg(image.id, { collect: !image.is_collected });
      //
      image.loading = false;
      if (image.is_collected) {
        this.$message({
          message: "",
          type: "success",
        });
      } else {
        this.$message({
          message: "",
          type: "success",
        });
      }
      //
      this.$nextTick(() => {
        this.loadImg(this.page);
      });
    },
    //
    onDelete(image) {
      //
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          image.deleteLoading = true;
          await deleteImg(image.id);
          image.deleteLoading = false;
          this.$message({
            message: "",
            type: "success",
          });
          //
          this.$nextTick(() => {
            this.loadImg(this.page);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  created() {
    this.loadImg();
  },
  mounted() {},
};
</script>
