.image-list-container {
  .nav-bar-btn {
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;
  }
  .img-list {
    margin-bottom: 20px;
    .img-col {
      position: relative;

      .img-item {
        padding-bottom: 20px;
      }
      .icon-btn {
        height: 40px;
        position: absolute;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        font-size: 20px;
        bottom: 24px;
        left: 5px;
        right: 3px;
        background-color: rgba(204, 204, 204, 0.5);
      }
    }
  }
}
.selected {
  position: absolute;
  left: 50px;
  right: 0;
  top: 0;
  bottom: 0;
  width: 150px;
  height: 120px;
  background: url("./selected.png") no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  // padding-left: 25px;
}
</style>
