<template>
  <div class="publish-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item
            ><a href="/">{{
              $route.query.id ? "" : ""
            }}</a></el-breadcrumb-item
          >
        </el-breadcrumb>
      </div>
      <el-form
        ref="publishForm"
        :model="article"
        label-width="60px"
        :rules="publishRules"
      >
        <el-form-item label="" prop="title">
          <el-input
            v-model="article.title"
            placeholder=">=5<=30"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!--  -->
          <el-tiptap
            v-model="article.content"
            :extensions="extensions"
            lang="zh"
            height="400px"
            placeholder=""
          />
          <!--  -->
        </el-form-item>
        <el-form-item label="">
          <!--  -->
          <el-radio-group v-model="article.cover.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <template v-if="article.cover.type > 0">
            <div class="upload-file">
              <!--  -->
              <!-- <upload-file
                v-for="(file, index) in article.cover.type"
                :key="index"
                @update-url="updateUrl(index, $event)"
                :coverImage="article.cover.images[index]"
              ></upload-file> -->
              <upload-file
                v-for="(file, index) in article.cover.type"
                :key="index"
                v-model="article.cover.images[index]"
              ></upload-file>
            </div>
          </template>
        </el-form-item>
        <!--  -->
        <el-form-item v-if="article.cover.type > 0">
          <el-alert
            title=""
            type="warning"
          >
          </el-alert>
        </el-form-item>
        <el-form-item label="" prop="channel_id">
          <!--  -->
          <el-select v-model="article.channel_id" placeholder="">
            <el-option
              :label="channel.name"
              :value="channel.id"
              v-for="channel in channelList"
              :key="channel.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <!--  -->
          <el-button type="primary" @click="onPublish(false)">{{
            $route.query.id ? "" : ""
          }}</el-button>
          <el-button @click="onPublish(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import {
  getChannel,
  publishArticle,
  getAssignArticle,
  updateAssignArticle,
} from "@/api/article";
import {
  //  extensions
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  Image,
  Fullscreen,
  HorizontalRule,
  Preview,
  TextColor,
} from "element-tiptap";
// import element-tiptap 
import "element-tiptap/lib/index.css";
import { getImgUrl } from "@/api/image.js";
import UploadFile from "./components/upload-file.vue";
export default {
  name: "publishIndex",
  components: {
    "el-tiptap": ElementTiptap,
    UploadFile,
  },
  props: {},
  data() {
    return {
      article: {
        title: "", //
        content: "", //
        cover: {
          type: 1, //-1:0-1-13-3
          images: [],
        },
        channel_id: null, //id
      },
      channelList: [], //
      //  extensions
      // 
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }), // 
        new Underline({ bubble: true, menubar: false }), // 
        new Italic(),
        new Image({
          async uploadRequest(file) {
            //formdata
            let fd = new FormData();
            fd.append("image", file);
            //url
            const { data: res } = await getImgUrl(fd);
            return res.data.url;
          },
        }),
        new Strike(),
        new HorizontalRule(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new TodoItem(),
        new TodoList(),
        new Fullscreen(),
        new Preview(),
        new TextColor(),
      ],
      //
      publishRules: {
        title: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 5,
            max: 30,
            message: " 5  30 ",
            trigger: "blur",
          },
        ],
        content: [
          {
            validator(rule, value, callback) {
              if (value === "<p></p>" || value === "") {
                callback(new Error(""));
              } else {
                callback();
              }
            },
          },
        ],
        channel_id: [
          {
            required: true,
            message: "",
          },
        ],
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    //routeid
    onPublish(draft) {
      this.$refs["publishForm"].validate(async (valid) => {
        if (!valid) {
          return;
        }
        const articleId = this.$route.query.id;
        if (articleId) {
          //
          await updateAssignArticle(articleId, this.article, draft);
          this.$message({
            message: "",
            type: "success",
          });
          this.$router.push("/article");
        } else {
          await publishArticle(this.article, draft);
          this.$message({
            message: "",
            type: "success",
          });
          this.article.title = "";
          this.article.content = "";
          this.$router.push("/article");
        }
      });
    },
    //
    async loadChannel() {
      const { data: res } = await getChannel();
      this.channelList = res.data.channels;
    },
    //
    async loadAssignArticle() {
      const { data: res } = await getAssignArticle(this.$route.query.id);
      this.article = res.data;
    },
    //url
    updateUrl(index, url) {
      this.article.cover.images[index] = url;
    },
  },
  created() {
    this.loadChannel();
    if (this.$route.query.id) {
      this.loadAssignArticle();
    }
  },
  mounted() {},
};
</script>
<style lang="less" scoped>
.upload-file {
  display: flex;
}
</style>
